import NewsApiService from './apiService.js';
import imageEl from '../templates/photo-cards.hbs';

const refs = {
    searchForm: document.querySelector('.search-form'),
    imagesContainer: document.querySelector('.gallery'),
    loadMoreBtn: document.querySelector('[data-action="load-more"]'),
};

const newsApiService = new NewsApiService();
console.log(newsApiService);

refs.searchForm.addEventListener('submit', onSearch);
refs.loadMoreBtn.addEventListener('click', onLoadMore);


function onSearch(e) {
  e.preventDefault();

    newsApiService.query = e.currentTarget.elements.query.value;

  if (newsApiService.query === '') {
    return alert('Введи что-то нормальное');
  }

  newsApiService.resetPage();
  clearImagesContainer();
  fetchImages();
}

function onLoadMore() {
  newsApiService.fetchImages().then(appendImagessMarkup);
  
  $('.container').infiniteScroll({
  // options
  path: '.pagination__next',
  append: '.post',
  history: false,
});
}

function fetchImages() {
  newsApiService.fetchImages().then(appendImagessMarkup);
}

function appendImagessMarkup(images) {
  refs.imagesContainer.insertAdjacentHTML('beforeend', imageEl(images));
}

function clearImagesContainer() {
  refs.imagesContainer.innerHTML = '';
}


